.page {
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    background-color: $white;
    font-family: IBM Plex Sans, sans-serif;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: $dark-teal;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease-in-out;
    z-index: 20;
  }

  &__menu:target {
    opacity: 1;
    pointer-events: all;
    max-height: 100%;
  }

  &::after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease-in-out;
    z-index: 10;
  }

  &:has(.page__menu:target)::after {
    opacity: 1;
  }

  &:has(.page__menu:target) {
    overflow: hidden;
    pointer-events: none;
  }
}
