.aside {
  background-color: $dark-teal;
  max-height: 0;
  overflow: auto;
  z-index: 5;
  transition: opacity 0.3s ease;

  &__content {
    @include pageGrid;
  }

  &__gallery {
    margin-bottom: 24px;
  }

  &__about-us {
    margin-bottom: 24px;
  }

  &__close-icon {
    background-image: url(../icons/icon-close.svg);
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
    position: relative;
    right: -1%;

    @include onTablet {
      right: 3%;
    }

    @include onDesktop {
      right: 5%;
    }

    @include onDesktopWild {
      right: 3%;
    }
  }

  &__days {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__text {
    @include text;

    line-height: 150%;
    text-decoration: none;
    color: $white;
  }

  &__titles {
    @include title;

    text-decoration: none;
    color: $white;
    font-weight: 500;
    transition: color 0.3s ease;

    &:hover {
      color: $red-orange;
    }

    @include onTablet {
      font-size: 16px;
    }

    @include onDesktop {
      font-size: 22px;
    }
  }

  &__time {
    font-weight: 600;
  }

  &__title {
    margin: 0 0 20px;
  }

  &__top {
    display: flex;
    gap: 38px;
    margin-top: 27px;
    grid-column: span 2;

    @include onTablet {
      gap: 34px;
      margin-top: 40px;
      height: 145px;
      border-right: 1px solid rgba(255, 255, 255, 0.5);
      grid-column: span 3;
    }

    @include onTabletWild {
      gap: 84px;
    }

    @include onDesktop {
      column-gap: 102px;
      grid-column: 1 / 7;
    }
  }

  &__line {
    background-color: rgba(255, 255, 255, 0.5);
    height: 1px;
    margin: 24px 20px;

    grid-column: 1 / -1;

    @include onTablet {
      display: none;
    }

    @include onDesktop {
      display: none;
    }
  }

  &__work {
    grid-column: 1 / -1;
    margin: 0 20px;

    @include onTablet {
      grid-column: 4 / -2;
      grid-row: 1;
      margin: 40px 0;
    }

    @include onDesktop {
      grid-column: 8 / -4;
    }
  }

  &__p {
    margin: 16px 0 27px 20px;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 5 / -1;
      grid-row: 1;
      margin: 80px 0 0 40px;
    }

    @include onDesktop {
      grid-column: -4 / -2;
      margin: 80px 0 0;
    }
  }
}
