.gallery {
  @include pageGrid;

  &__picture-4 {
    width: 100%;
    height: 100%;
    display: none;

    @include onDesktop {
      display: block;
    }
  }

  &__picture-wrapper-4 {
    overflow: hidden;
    height: 410px;
    grid-column: 1 / -1;
    display: none;

    @include onDesktop {
      grid-column: 1 / 5;
      height: 600px;
      margin-bottom: 30px;
      display: block;
    }
  }

  &__picture-5 {
    width: 100%;
    height: 100%;
    display: none;

    @include onDesktop {
      display: block;
    }
  }

  &__picture-wrapper-5 {
    display: none;
  }

  &__picture-6 {
    width: 100%;
  }

  &__picture-wrapper-6 {
    overflow: hidden;
    display: none;

    @include onDesktop {
      height: 600px;
      display: block;
      margin-bottom: 30px;
      grid-column: 5 / -1;
    }
  }

  &__picture-7 {
    height: 100%;
    width: 100%;
  }

  &__picture-wrapper-7 {
    overflow: hidden;
    display: none;

    @include onDesktop {
      display: block;
      grid-column: 1 / -5;
    }
  }

  &__picture-8 {
    width: 100%;
    height: 100%;
  }

  &__picture-wrapper-8 {
    overflow: hidden;
    display: none;

    @include onDesktop {
      display: block;
      grid-column: -5 / -1;
    }
  }

  &__h2 {
    margin: 100px 0 40px;

    @include onTablet {
      margin-top: 228px;
      text-align: left;
    }

    @include onDesktop {
      margin-top: 402px;
    }
  }
}
