.schedule {
  @include pageGrid;

  position: relative;

  &__logo {
    justify-content: left;
    margin: 50px 0 40px;
    width: 63px;
    grid-column: 1 / 1;
    transition: transform .3s ease;

    &:hover {
      transform: translateY(-2px);
    }

    @include onTablet {
      grid-column: 1 / 1;
    }
  }

  &__socials {
    display: flex;
    gap: 20px;
    margin: 74px 0 64px;
    margin-left: auto;

    @include onTablet {
      margin: 0;
      position: absolute;
      top: 185px;
    }
  }

  &__fb,
  &__instagram {
    width: 24px;
    height: 24px;
    transition: transform .3s ease;

    &:hover {
      transform: translateY(-2px);
    }
  }

  &__title {
    @include text;

    margin: 40px 0 20px;
    color: $charcoal-black;
    line-height: 150%;

    @include onTablet {
      margin: 50px 0 20px;
      line-height: 140.63%;
    }

    @include onDesktop {
      margin: 0 0 20px;
    }
  }

  &__day,
  &__day--p,
  &__adres,
  &__link {
    all: initial;
    color: $charcoal-black;
    line-height: 150%;
  }

  &__days {
    display: flex;
    flex-direction: column;
  }
  &__adres {
    @include text;

    display: inline-block;
  }


  &__href {
    @include text;

    text-decoration: none;
    color: inherit;
    cursor: pointer;

    &:hover {
      color: $red-orange;
    }
  }

  &__days,
  &__addresses {
    margin: 0;
    padding: 0;
  }

  &__days {
    display: flex;
    flex-wrap: wrap;
  }

  &__day--p {
    @include text;

    margin: 30px 0 0;
  }

  &__time {
    @include text;

    font-weight: 600;
  }

  &__day {
    @include text;
  }

  &__line {
    height: 1px;
    background-color: $dark-teal;
    margin: 70px 0 40px;

    grid-column: 1 / -1;

    @include onTablet {
      margin: 64px 0;
      grid-column: 2 / -2;
    }

    @include onDesktop {
      display: none;
    }
  }

  &__work {
    @include onTablet {
      grid-column: 2 / 4;
      grid-row: 1;
    }

    @include onDesktop {
      margin-top: 50px;
      grid-column: 3 / 5;
    }
  }

  &__contacts {
    @include onTablet {
      grid-column: 4 / -2;
      grid-row: 1;
    }

    @include onDesktop {
      height: 260px;
      padding-right: 65px;
      margin-top: 50px;
      border-right: 1px solid $dark-teal;
      grid-column: 5 / 8;
    }
  }

  &__galery {
    padding-bottom: 20px;
    grid-column: 1 / 1;

    @include onTablet {
      grid-column: 2 / 4;
    }

    @include onDesktop {
      margin-top: 50px;
      padding: 0;
      grid-column: 9 / 11;
      grid-row: 1;
    }
  }

  &__events {
    grid-column: 1 / 1;

    @include onTablet {
      grid-column: 2 / 4;
    }

    @include onDesktop {
      margin-top: 80px;
      grid-column: 9 / 11;
      grid-row: 1;
    }
  }

  &__about-us {
    padding-bottom: 20px;
    grid-column: 2 / 2;

    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onDesktop {
      margin-top: 50px;
      padding: 0;
      grid-column: 11 / 12;
      grid-row: 1;
    }
  }

  &__contact {
    grid-column: 2 / 2;

    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onDesktop {
      margin-top: 80px;
      grid-column: 11 / 12;
      grid-row: 1;
    }
  }

  &__text {
    @include text;

    text-decoration: none;
    color: $charcoal-black;
  }
}
